import React from 'react';
import './MenuIcon.css';

class MenuIcon extends React.Component {

  constructor(props) {
    super(props);
    this.state = { 
      styleCondition: true,
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick = () => {
    this.setState(prevState => ({ styleCondition: !prevState.styleCondition }));
    console.log(this.state);
  }

  render () {
    return ( 
      <div className={`menuicon ${this.state.styleCondition ? "" : " change"}`} onClick={this.handleClick}>
        <div className="bar1"></div>
        <div className="bar2"></div>
        <div className="bar3"></div>
      </div>
    );
  }
}

export default MenuIcon;
